.slider section {
    position: absolute;
    bottom: 12%;
    padding-left: 2rem;
}
.slider h3 {
    font-size: 1.8rem;
    font-weight: 700;
    text-shadow: 0 0.1rem 0.6rem #222;
}
.img {
    width: 100%;
    height: 40rem;
}

@media screen and (min-width: 320px) {
    .slider h3 {
        font-size: 2rem;
    }
}
@media screen and (min-width: 540px) {
    .slider h3 {
        font-size: 2.4rem;
    }
}
@media screen and (min-width: 1024px) {
    .slider section {
        padding-left: 2.8rem;
        bottom: 14%;
    }
    .slider h3 {
        font-size: 3.2rem;
    }
    .img {
        height: 0;
        padding-bottom: 38%;
    }
}
@media screen and (min-width: 1800px) {
    .slider section {
        padding-left: 3.6rem;
        bottom: 14%;
    }
    .slider h3 {
        font-size: 4.4rem;
    }
    .img {
        padding-bottom: 39%;
    }
}