.Modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
}

.modalBody {
    position: absolute;
    width: 80%;
    height: 40rem;
    padding: 2.4rem;
    text-align: center;
    background-color: #000;
    border-radius: 1rem;
    box-shadow: 0 .2rem .3rem 0 rgba(34, 36, 38, 0.15);
    z-index: 999;
}

.modalCloseBtn {
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    border: none;
    color: var(--txt-color);
    background-color: transparent;
    font-size: 2rem;
    z-index: 1000;
}

.modalCloseBtn:hover {
    cursor: pointer;
}

@media screen and (min-width: 375px) {
    .modalBody {
        width: 60%;
        padding: 4rem;
    }
}