.container {
    width: 80%;
    margin: 0 auto;
    margin-bottom: 4.6rem;
}
.container {
    line-height: 1.5;
}
.container .img {
    padding-top: 44.25%;
    margin-bottom: 1.6rem;
}
.genre {
    font-size: 1.2rem;
    border-radius: 0.2rem;
    border: .1rem solid var(--txt-color);
    margin: 0 1rem .6rem 0;
    padding: .2rem 1rem;
}

.container h1 {
    font-size: 2.4rem;
    font-weight: 700;
    margin: .8rem 0 .4rem;
}
.container .flex {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
}
.subtitle {
    display: flex;
    flex-flow: column;
}

.container .star {
    display: flex;
    font-size: 1.3rem;
    margin-top: 1rem;
}
.container .star>div>span:last-child {
    margin-right: .8rem;
} 

.oriTitle {
    font-size: 1rem;
    color: #dedede;
    margin-right: 1.2rem;
}

/* Info */
.info {
    margin-top: 1.4rem;
    font-size: 1.3rem;
    color: #eee;
}
.info p:nth-child(1) {
    margin-right: 1.2rem;
}

.container .overview {
    margin: 1.2rem 0 4rem;
    font-size: 1.4rem;
} 

.loading {
    font-size: 6rem;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
}

@media screen and (min-width: 375px) {
    .container h1 {
        font-size: 2.8rem;
        font-weight: 700;
        margin-bottom: .4rem;
    }
    .oriTitle {
        font-size: 1.2rem;
    }

    /* Info */
    .info {
        font-size: 1.4rem;
    } 
    .container .overview {
        margin: 1.2rem 0 4rem;
        font-size: 1.6rem;
    }
}
@media screen and (min-width: 540px) {
    .subtitle {
        flex-flow: row;
    }
    .container h1 {
        font-size: 2.8rem;
        font-weight: 700;
        margin-bottom: .6rem;
    }
    .container .star {
        margin-top: 0;
    }

    /* Info */
    .info {
        margin-top: 1rem;
    }
}
@media screen and (min-width: 768px) {
    .container .star {
        font-size: 1.3rem;
    }

}
@media screen and (min-width: 1024px) {
    .genre {
        font-size: 1.8rem;
    }
    .container h1 {
        font-size: 4rem;
    }
    .oriTitle {
        font-size: 1.7rem;
        margin-right: 1.6rem;
    }
    .container .star {
        font-size: 1.7rem;
    }
    .container .star>div>span>svg {
        width: 2.4rem;
        height: 2.4rem;
    }
    .container .overview {
        font-size: 1.8rem;
    } 
    /* Info */
    .info {
        font-size: 1.6rem;
    } 

}