.container {
  font-size: 1.4rem;
  padding-top: 6.2rem;
  color: var(--txt-color);
}
.container input {
  width: 68%;
  padding: 1.2rem;
  margin-top: 16rem;
  font-size: 2.8rem;
  color: var(--txt-color);
  background: transparent;
  border-bottom: .2rem solid var(--txt-color);
}

@media screen and (min-width: 375px) {
  .container input {
    font-size: 3.2rem;
  }
  .container {
    font-size: 1.6rem;
  }
}