.noImg {
    position: relative;
    overflow: hidden;
};
.noImg img::after {
    display: none;
}
.txt {
    display: none;
}

@media screen and (min-width: 375px) {
    .txt {
        display: none;
    }
    .noImg img::after {
        display: block;
        content: "준비중";
        font-size: 1.4rem;
        width: 50%;
        position: absolute;
        top: calc( 50% - 2rem );
        left: 50%;
        transform: translate(-50%);
        color: white;
        padding: 1rem 2rem;
        text-align: center;
        border: .05rem solid rgba(0, 128, 128, .5);
        border-radius: .2rem;
        box-shadow: teal 0 0 17rem .4rem;
        line-height: 1.5;
    }
}
@media screen and (min-width: 540px) {
    .txt {
        display: block;
        font-size: 1.2rem;
        width: 50%;
        position: absolute;
        top: calc( 50% - 3rem );
        left: 50%;
        transform: translate(-50%);
        color: white;
        padding: 1rem 2rem;
        text-align: center;
        border: .05rem solid rgba(0, 128, 128, .5);
        border-radius: .2rem;
        box-shadow: teal 0 0 17rem .4rem;
        line-height: 1.5;
    }
    .noImg::after {
        display: none;
    }
}
@media screen and (min-width: 768px) {
    .txt {
        top: calc( 50% - 2rem );
    }
}